@import url(https://fonts.googleapis.com/css?family=Montserrat);

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.App {
  min-width:1000px;
  height: 100vh;
  text-align: center; 
  /*background-image: radial-gradient(circle,#760A7A,#570363);
  color:white;*/
  position: relative;
}

body {
  font-family: Montserrat, cursive;
  font-size: 14px;
  background: #ecf0f1;
}

#nav-bar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #111111;
}

#nav-bar li {
  float: left;
}

#nav-bar li a {
  display: block;
  color: #fff;
  text-align: center;
  padding: 16px;
  text-decoration: none;
}

#nav-bar li a:hover {
  background-color: #1d1c1c;
}

#nav-bar li a.selected {
  background-color: #333333;
}



.container {
  width: 100%;
  height: calc((100% - 50px));
  padding: 20px;
  box-sizing: border-box;
}

.container input, select, textarea:focus, input:focus, select:focus {
background-color: none;
-webkit-rtl-ordering: logical;
cursor: text;
padding: 1px;
border-style: none;
-webkit-border-image: none;
        border-image: none;
outline: none;
border-bottom: 1px solid grey;
background: none;
font-family: Montserrat, cursive;
font-size: 14px;
min-height: 20px;
width: 130px;
}

#grid-header, .item  {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  justify-content: start;
  align-content: space-evenly;
  height: 25px;
  padding: 10px;
}

#grid-header {
  background: rgb(112, 14, 168);
  margin-top: 20px;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.3);
  display:grid;
  color: white;
}

.item {
  border-bottom: 1px solid rgba(153, 151, 151, 0.3);
}

.row-bar {
  width: 100%;
}

.search-bar {
  width: 70%;
  display: inline-block;
  text-align: left;
}

.pagination, .itemsPerPage, .total {
  width: 10%;
  display: inline-block;
  text-align: right;
}

.itemsPerPage select {
  width: 50px;
  color: black;
  text-align: right;
}

.itemsPerPage select:focus {
  width: 50px;
}

.dot {
  height: 100px;
  width: 100px;
  background-image: -webkit-radial-gradient(rgb(163, 72, 172), rgb(112, 14, 168), 
  rgb(50, 6, 75) );
  background-image: radial-gradient(rgb(163, 72, 172), rgb(112, 14, 168), 
  rgb(50, 6, 75) );
  border-radius: 50%;
  display: block;
  position: fixed;
  bottom: 50px;
  right: 100px;
  color: white;
  padding: 8px;
  box-sizing: border-box;
}
.form {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 360px;
  height: 500px;
  margin: auto;
  background: black;
  opacity: 0.9;
  border-radius: 5px;
  color: white;
  padding: 10px;
  box-sizing: border-box;
}

.form > div {
  min-height: 30px;
}

.close-button {
  text-align: right;
}

.form-input {
  display: grid;
  grid-template-columns: 120px 160px;
  color: white;
  justify-content: center;
  width: 100%;
  text-align: left;
  align-content: space-evenly;
}

.form-input > div {
  height: 50px;
}

.one-column1 {
  grid-column: 1 / span 2;
 }
 
.one-column1 > div {
   display: inline-block;
   width: 100px;
}

.form-input input {
  color: white;
  width: 140px;
}

.form-input select {
  color: white;
  width: 140px;
}

.form-input option {
  color: white;
  width: 140px;
}

input[type="radio"],input[type="radio"]:focus  {
  width: 20px;
  min-height: 10px;
  font-size: 10px
}

.sel option {
  padding: 5px 8px;
  color: white;
  background: rgb(59, 59, 59);
  border-style: none;
  -webkit-border-image: none;
          border-image: none;
  border: none;
  outline: none;
  width: 140px;
}

.sel select {
  width: 140px;
  color: white;
}

.sel select:focus {
  width: 140px;
  color: white;
}

.form button {
  outline: none;
  background: none;
  border: none;
  padding: 0px;
  background:rgb(112, 14, 168);
  width: 80px;
  height: 45px;
  border-radius: 3px;
  opacity: 0.9;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.3);
  font-family: Montserrat, cursive;
  color: white;
  font-size: 16px;
}

.red > input {
  color: red;
}
.delete-form {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 300px;
  height: 150px;
  margin: auto;
  background: black;
  opacity: 0.9;
  border-radius: 5px;
  color: white;
  padding: 10px;
  box-sizing: border-box;
}

.delete-form > div {
  min-height: 30px;
}

.close-button {
  text-align: right;
}

.one-column {
  grid-column: 1 / span 2;
}

.one-column > div {
  display: inline-block;
  width: 160px;
}

.delete-form .div-button {
  padding-top: 10px;
}

.delete-form button {
  outline: none;
  background: none;
  border: none;
  padding: 0px;
  background:rgb(112, 14, 168);
  width: 80px;
  height: 45px;
  border-radius: 3px;
  opacity: 0.9;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.3);
  font-family: Montserrat, cursive;
  color: white;
  font-size: 16px;
}
.home {
  /*background-image: url("../img/explosion-of-coins.jpg");*/
  background-image: -webkit-radial-gradient(circle,rgb(183, 16, 189), #760A7A, #570363);
  background-image: radial-gradient(circle,rgb(183, 16, 189), #760A7A, #570363);
  width: 100%;
  height: calc((100% - 50px));
}

.text {
  margin: auto;
  width: 700px;
  padding-top: 50px;
  font-size: 20px;
  color: white;
}

.text ul {
  background: inherit;
  list-style-type: none;
}

.text img {
  text-align: center;
}


.text {
  margin: auto;
  width: 700px;
  padding-top: 5vh;
  font-size: 20px;
}

.chart-box {
  width: 600px;
  height: 500px;
  padding: 20px;
  text-align: left;
  display: inline-block;
}

.chart-box .select-month {
  padding-left: 30px;
  padding-bottom: 30px;
}

.select-month select, select:focus {
  background-color: none;
  -webkit-rtl-ordering: logical;
  cursor: text;
  padding: 1px;
  border-style: none;
  -webkit-border-image: none;
          border-image: none;
  outline: none;
  border-bottom: 1px solid grey;
  background: none;
  font-family: Montserrat, cursive;
  font-size: 14px;
  min-height: 20px;
  color: black;
  width: 80px;
}

.select-month option {
  padding: 5px 8px;
  color: rgb(15, 14, 14);
  background: rgb(224, 222, 222);
  border-style: none;
  -webkit-border-image: none;
          border-image: none;
  border: none;
  outline: none;
}

.chart-box .chart-name {
  font-size: 20px;
  text-align: center;
  padding-bottom: 10px;
}

.recharts-wrapper {
  margin: auto;
}

.chart-box p, ul {
  background: white ;
}

.recharts-legend-wrapper ul {
  background: #1b1b1b;
  color: white;
}
